footer{
    height: auto;
    background-color: #111010;
    padding: 0px;
    z-index: 1000;
    width: 100%;
}

footer h1{
    font-size: 1rem;
}

.navcontainer{
    width: 25%;
}




.nav li{
    margin: 5px 0px;
    cursor: pointer;
    padding: 1px 6px;
    transition: 0.4s;
    width: auto;
}


.nav li:hover{
    background-color: #fff;
    color: #000;
}



@media(max-width: 900px){
    footer h1{
        font-size: 14px;
    }

    .navfooter{
        flex-direction: column;
        text-align: center;
    }

    .navcontainer{
        width: 100%;
        margin: 10px 0px;
        padding-bottom: 20px;
        border-bottom: 1px solid white;
    }
}
